<template>

  <div class="widget-courselist">
    <div class="panel-title">我的博客</div>
    <ul class="pagelist-wrapper">

      <li class="impression-item impression-viewed" data-post-id="66420781" v-for="item in model" :key="item._id">


        <a href="https://baixiaosheng.tuchong.com/t/66420781/" class="item-cover" target="_blank">
          <img :src="item.cover">
        </a>
        <div class="item-info" style="width: 450px;">
          <a href="https://baixiaosheng.tuchong.com/t/66420781/" class="item-title"  target="_blank">{{item.title}}</a>
          <p class="item-desc">{{item.introduction}}</p>
        </div>


        <div class="item-related">
          <div class="item-tags has-tags">
            <a @click="blogDetail(item.theme._id)" v-for="tagitem in item.tag"   class="event-tag popover-action" >{{tagitem.name}}</a>
          </div>
          <div class="item-handler">
            <span>{{item.author.name}}</span>

            <span> |

				25926阅读


				· 553喜欢


				· 68评论

			</span>

          </div>
        </div>
      </li>

    </ul>
    <i class="icon-loading--yellow" style="display: none;"></i>
    <div class="pagelist-load-more" style="display: block;">已经无数据了</div>
    <i class="icon-end" style="display: none;"></i>
  </div>
</template>

<script>
  export default {
    name: "MyBlog",
    data(){
      return{
        model:{},
        id:''
      }
    },
    methods:{
      async fetch(){

        let res = await this.$http.get('/rest/blog')


        this.model = res.data.filter(obj=>{


          return obj.hasOwnProperty('author') && obj.author._id === this.id
        })



      },
    },
    created(){
      this.id =  localStorage.user
      this.fetch()
    }
  }
</script>

<style lang="scss" scoped>


  .widget-courselist {

    padding: 34px 50px 0 30px;
    border: 1px #d9d9d9 solid;
    .panel-title {
      width: 100%;
      border-bottom: 1px #d9d9d9 solid;
      margin-bottom: 10px;
      color:#ff5f5f;
      font-size: 18px;
      font-family: 'PingFangSC','helvetica neue','hiragino sans gb','arial','microsoft yahei ui','microsoft yahei','simsun','sans-serif'!important;
      font-weight: bold;
    }
    .pagelist-wrapper {
      list-style: none;
      padding: 0;
      margin: 0;

      li {
        padding: 32px 0 20px;
        border-top: 1px solid #eaeaea;
        font-size: 0;

        .item-cover {
          position: relative;
          display: inline-block;
          width: 150px;
          height: 150px;
          margin-right: 30px;

          img {
            display: block;
            width: 100%;
            height: 100%;
          }
        }

        .item-info {
          display: inline-block;
          width: 100%;
          vertical-align: top;
          font-size: 14px;
          overflow: hidden;

          .item-title {
            display: block;
            width: 100%;
            font-size: 22px;
            line-height: 1;
            color: #259;
            font-weight: 700;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }

          .item-desc {
            max-height: 84px;
            padding: 0;
            margin: 20px 0 0;
            color: #9a9a9a;


          }
        }

        .item-related {
          position: relative;
          display: block;
          margin: 10px 0 0;
          font-size: 12px;
          width: 630px;


          .item-tags {
            height: 22px;
            padding-right: 300px;
            font-size: 0;
            overflow: hidden;


            &::before {

              content: " ";
              display: inline-block;
              width: 20px;
              height: 20px;
              background-image: url(data:image/png;base64,);
              background-position: -103px 81px;
              vertical-align: middle;

            }

            a {
              display: inline-block;
              height: 22px;
              padding: 0 12px;
              border: 1px solid #e3e4e6;
              -webkit-border-radius: 11px;
              -moz-border-radius: 11px;
              border-radius: 11px;
              margin-left: 10px;
              color: #444444;
              font-size: 12px;
              text-align: center;
              line-height: 22px;
              vertical-align: middle;
            }
          }

          .item-handler {
            position: absolute;
            top: 3px;
            right: 0;

            span {
              color: #808388;
            }
          }
        }
      }
    }
  }
</style>